.journal__main-content{
    display: flex;

}
.journal__sidebar{
    background-color: $dark-pink;
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
    width: 475px;
}
.journal__sidebar-navbar{
    display: flex;
    justify-content: space-between;
}
.journal__sidebar-navbar h3{
    font-weight: lighter;
}
.journal__new-entry{
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 30px;
    width: 100%;

    transition: color .3s  ease;
    &:hover{
        color: darken($color: white, $amount: 20);
    }
}
.journal__entries{
    flex: 1 1 auto;
    margin-top: 30px;
    padding: 5px;
    overflow-y: scroll;
}
.journal__entry{
    background-color: white;
    border-radius: 4px;
    color: $dark-pink;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    overflow: hidden;
    &:hover{
        background-color: darken($color: white, $amount: 20);
    }


}
.journal__entry-picture{
    height: 75px;
    width: 75px;

}
.journal__entry-body{
    padding: 10px;
}
.journal_entry-tittle{
   
    font-size: 14px;
    font-weight: bold;


}
.journal_entry-content{
    font-size: 10px;
}
.journal__entry-date-box{
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px;
}
.journal__entry-date-box span{
    font-size: 12px;
}